<template>
  <div class="Location">
    <h3>Location</h3>
    <li>Location URL URL</li>
    <img src="../assets/url.png" />
    <pre>
    console.log(location.href); //url
    console.log(location.host); //()
    console.log(location.port); //
    console.log(location.pathname); //
    console.log(location.search); // ?name=""&age=18
    console.log(location.hash); // #
    </pre>
    <h3></h3>
    <pre>
      var btn = document.querySelector(".gobd");
      btn.onclick = function () {
        location.href = "http://www.baidu.com";
      };
      <button class="gobd"></button></pre>
    <h3>-- 5</h3>
    <p class="p"></p>
    <pre>
      var p = document.querySelector(".p");
      var btn = document.querySelector(".fanhui");
      btn.onclick = function () {
        var time = 5;
        function dsq() {
          if (time == 0) {
            clearInterval(timer);
            location.href = "http://www.baidu.com";
          } else {
            p.innerText = time;
            time--;
          }
        }
        dsq();
        var timer = setInterval(dsq, 1000);
      };
      <button class="fanhui"></button></pre>
    <h3>--</h3>
    <pre>
        //
        <img src="../assets/login.png" >
        //
        <img src="../assets/index.png">
        //
        <img src="../assets/jg.png" >
      </pre>
    <h3>location</h3>
    <ul>
      <li>assign() </li>
      <li>replace() </li>
      <li>reload()  true</li>
    </ul>
    <pre>
        location.assign("http://www.baidu.com")
        <button @click="assign">assign</button>
        location.replace("http://www.baidu.com");
        <button @click="replace">replace</button>
        location.reload(true);
        <button @click="reload">replace</button></pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    gobd() {
      var btn = document.querySelector(".gobd");
      btn.onclick = function () {
        location.href = "http://www.baidu.com";
      };
    },
    fanhui() {
      var p = document.querySelector(".p");
      var btn = document.querySelector(".fanhui");
      btn.onclick = function () {
        var time = 5;
        function dsq() {
          if (time == 0) {
            clearInterval(timer);
            location.href = "http://www.baidu.com";
          } else {
            p.innerText = time;
            time--;
          }
        }
        dsq();
        var timer = setInterval(dsq, 1000);
      };
    },
    assign() {
      location.assign("http://www.baidu.com");
    },
    replace() {
      location.replace("http://www.baidu.com");
    },
    reload() {
      location.reload(true);
    },
  },
  computed: {},
  mounted() {
    this.gobd();
    this.fanhui();
    console.log(location.href); //url
    console.log(location.host); //()
    console.log(location.port); //
    console.log(location.pathname); //
    console.log(location.search); // ?name=""&age=18
    console.log(location.hash); // #
  },
};
</script>

