<template>
  <div class="Dffects">
    <h3>offset</h3>
    <li></li>
    <li></li>
    <hr />
    <li>offsetParent </li>
    <li>offsetTop </li>
    <li>offsetLeft </li>
    <li>offsetWidth  padding </li>
    <li>offsetHeight  padding </li>
    <div class="pianyi"></div>
    <pre>
      var div = document.querySelector(".pianyi");
      console.log(div.offsetTop);
      console.log(div.offsetLeft);
      //border padding width 
      console.log(div.offsetWidth);
      console.log(div.offsetHeight);
      //,body
      console.log(div.offsetParent);
      <button @click="pianyi"></button></pre>
    <h3>offsetstyle</h3>
    <ol>
      <li>style  ()</li>
      <li>offset</li>
    </ol>
    <hr />
    <ol>
      <li>style </li>
      <li>offset </li>
    </ol>
    <hr />
    <ol>
      <li>style.width paddingborder</li>
      <li>offsetWidth paddingborder</li>
    </ol>
    <hr />
    <ol>
      <li>style  ()</li>
      <li>offset  ()</li>
    </ol>
    <h3>--</h3>
    <div class="box"></div>
    <pre>
      //document
      document.onclick = function (e) {
        //
        var bodyLeft = e.pageX;
        console.log("", bodyLeft);
        //
        var box = document.querySelector(".box");
        var boxLeft = box.offsetLeft;
        console.log("(body)" + boxLeft);
        //body =
        console.log("", bodyLeft - boxLeft);
      };
    <button @click="zuobiao"></button></pre>
    <h3>--</h3>
    <pre>
      <button @click="tanchu"></button>
          var a = document.querySelector(".tanchu");
          //
          a.addEventListener("mousedown", function (e) {
            //
            var x = e.pageX - a.offsetLeft;
            var y = e.pageY - a.offsetTop;
            // left top
            function fn(e) {
              a.style.left = e.pageX - x + "px";
              a.style.top = e.pageY - y + "px";
            }
            // topleft
            document.addEventListener("mousemove", fn);
            //,document
            document.addEventListener("mouseup", function () {
              document.removeEventListener("mousemove", fn);
            });
          })
    </pre>
    <div class="quanp" v-if="flg">
      <div class="tanchu">
        
        <span @click="flg = !flg"></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      flg: false,
    };
  },
  methods: {
    pianyi() {
      var div = document.querySelector(".pianyi");
      console.log(div.offsetTop);
      console.log(div.offsetLeft);
      console.log(div.offsetWidth);
      console.log(div.offsetHeight);
      console.log(div.offsetParent);
    },
    zuobiao() {
      //document
      document.onclick = function (e) {
        //
        var bodyLeft = e.pageX;
        console.log("", bodyLeft);
        //
        var box = document.querySelector(".box");
        var boxLeft = box.offsetLeft;
        console.log("(body)" + boxLeft);
        //body =
        console.log("", bodyLeft - boxLeft);
      };
    },
    tanchu() {
      this.flg = !this.flg;
      setTimeout(() => {
        var a = document.querySelector(".tanchu");
        a.addEventListener("selectstart", function (e) {
          e.preventDefault();
          //
          a.addEventListener("mousedown", function (e) {
            //
            var x = e.pageX - a.offsetLeft;
            var y = e.pageY - a.offsetTop;
            // left top
            function fn(e) {
              a.style.left = e.pageX - x + "px";
              a.style.top = e.pageY - y + "px";
            }
            // topleft
            document.addEventListener("mousemove", fn);
            //,document
            document.addEventListener("mouseup", function () {
              document.removeEventListener("mousemove", fn);
            });
          });
        });
      }, 0);
    },
  },
  computed: {},
  mounted() {},
};
</script>

<style>
.pianyi {
  width: 100px;
  height: 100px;
  background-color: pink;
  text-align: center;
  line-height: 100px;
  margin: 50px;
}
.box {
  width: 100px;
  height: 100px;
  background-color: pink;
  text-align: center;
  line-height: 100px;
  margin: 50px;
}
.tanchu {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 300px;
  height: 200px;
  line-height: 200px;
  background-color: pink;
  text-align: center;
  cursor: move;
}
.tanchu span {
  position: absolute;
  right: 0;
  top: 0;
  line-height: 30px;
  transform: translate(50%, -50%);
  background-color: rgb(255, 2, 2);
  border-radius: 50%;
  cursor: pointer;
}
.quanp {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
}
</style>