<template>
  <div class="get">
    <h3 id="h3">id</h3>
    <pre>
       var a = document.getElementById("h3")
       console.log(a);
       <button @click="byid"></button></pre>
    <h3></h3>
    <pre>
      var a = document.getElementsByTagName("h3");
      console.log(a);
      <button @click="name"></button></pre>
    <h3 class="h3">class</h3>
    <pre>
      var a = document.getElementsByClassName("h3");
      console.log(a);
       <button @click="byclass"></button></pre>
    <h3></h3>
    <pre>
      //
      var a = document.querySelector(".h3");
      console.log(a);
      <button @click="qslt"></button></pre>
    <h3> 2</h3>
    <pre>
      //
       var a = document.querySelectorAll("h3");
      console.log(a);
      <button @click="qslt2"></button></pre>
    <h3>body</h3>
    <pre>
      var a = document.body;
      console.log(a);
        <button @click="body"></button></pre>
    <h3>html</h3>
    <pre>
      var a = document.documentElement;
      console.log(a);
        <button @click="html"></button></pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    main() {},
    byid() {
      var a = document.getElementById("h3");
      console.log(a);
    },
    name() {
      var a = document.getElementsByTagName("h3");
      console.log(a);
    },
    byclass() {
      var a = document.getElementsByClassName("h3");
      console.log(a);
    },
    qslt() {
      var a = document.querySelector(".h3");
      console.log(a);
    },
    qslt2() {
      var a = document.querySelectorAll("h3");
      console.log(a);
    },
    body() {
      var a = document.body;
      console.log(a);
    },
    html() {
      var a = document.documentElement;
      console.log(a);
    },
  },
  mounted() {
    this.main();
  },
  computed: {},
};
</script>

