<template>
  <div class="Qitabom">
    <h3>navigator</h3>
    <li>userAgent</li>
    <pre>
      console.log(navigator);
      console.log(navigator.userAgent);
      <button @click="ceshi"></button></pre>
    <h3>history</h3>
    <li>back() </li>
    <li>forward() </li>
    <li>go() </li>
    <li>()</li>
    <pre>
         history.back();
        <button @click="back">back</button>
         history.forward();
        <button @click="forward">forward</button>
         //+ - 
         history.go(-1);
        <button @click="go">go</button></pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    ceshi() {
      console.log(navigator);
      console.log(navigator.userAgent);
    },
    back() {
      history.back();
    },
    forward() {
      history.forward();
    },
    go() {
      //+ - 
      history.go(-1);
    },
  },
  computed: {},
  mounted() {},
};
</script>

