<template>
  <div class="set">
    <h3></h3>
    <pre>
       var a = document.getElementById("clickId");
       a.onclick = function () {
       console.log("");
      };
    <button id="clickId" @click="ck"></button></pre>
    <h3></h3>
    <pre>
      <img src="../assets/on.png" ></pre>
    <h3></h3>
    <li>innerText</li>
    <pre> 
      //innerText,()
      var a = document.querySelector("#setpre");
      a.onclick = function () {
        a.innerText = "";
      };
      <button id="setpre" @click="sethtml"></button></pre>
    <h3></h3>
    <li>innerHtml</li>
    <pre> 
      //innerHTMLhtml()
      var a = document.querySelector("#setpre");
      a.onclick = function () {
        a.innerHTML = "";
      };
      <button id="setpre2" @click="sethtml2"></button></pre>
    <h3></h3>
    <ul>
      <li>src</li>
      <li>href</li>
      <li>id</li>
      <li>alt</li>
      <li>title</li>
    </ul>
    <a id="a" target="_block" href="http://www.baidu.com"></a>
    <img class="img" title="" src="../assets/on.png" alt="" />
    <pre>
      var a = document.querySelector("#a");
      a.href = "http://www.4399.com";
      a.id = "b"
      var b = document.querySelector(".img");
      b.title = "";
      b.src = "https://gitee.com/elfin-chen/bed/raw/master/img/20210612202700.jpg"
      b.alt = ""
      <button @click="changjian"></button></pre>
    <h3></h3>
    <li>value</li>
    <li>checked</li>
    <li>selected</li>
    <li>disabled</li>
    <li>type</li>
    <input type="password" id="ipt" />
    <pre>
      var a = document.querySelector("#ipt");
      a.value = "123";
      a.disabled = true;
      a.type = "text"
      <button @click="biaodan"></button></pre>
    <h3></h3>
    <div class="style"></div>
    <pre>
      var a = document.querySelector(".style");
      //class 
      a.className = "stylegai";
      //js
      //
      a.style.width = "200px";
      a.style.height = "200px";
      a.style.backgroundColor = "red";
      a.style.borderRadius = "20px";
      <button @click="setstyle"></button></pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    ck() {
      var a = document.getElementById("clickId");
      a.onclick = function () {
        console.log("");
      };
    },
    sethtml() {
      var a = document.querySelector("#setpre");
      a.innerText = "";
    },
    sethtml2() {
      var a = document.querySelector("#setpre2");
      a.innerHTML = "";
    },
    changjian() {
      var a = document.querySelector("#a");
      a.href = "http://www.4399.com";
      a.id = "b";
      var b = document.querySelector(".img");
      b.title = "";
      b.src =
        "https://gitee.com/elfin-chen/bed/raw/master/img/20210612202700.jpg";
      b.alt = "";
    },
    biaodan() {
      var a = document.querySelector("#ipt");
      a.value = "123";
      a.disabled = true;
      a.type = "text";
    },
    setstyle() {
      var a = document.querySelector(".style");
      //class 
      a.className = "stylegai";
      //js
      //
      a.style.width = "200px";
      a.style.height = "200px";
      a.style.backgroundColor = "red";
      a.style.borderRadius = "20px";
    },
  },
};
</script>

<style lang="less" scoped>
.img {
  width: 100px;
  height: 100px;
}
.style {
  width: 100px;
  height: 100px;
  background-color: pink;
}
</style>