<template>
  <div class="Animatior">
    <h3>Animation</h3>
    <div class="donghua" v-show="flg"></div>
    <pre>
      function donghua(obj, target) {
        var timer = setInterval(function () {
          if (obj.offsetLeft >= target) {
            clearInterval(timer);
          }
          obj.style.left = obj.offsetLeft + 1 + "px";
        }, 30);
      }
      var box = document.querySelector(".donghua");
      donghua(box, 600);
      <button @click="fengzhuang"></button> </pre>
    <h3></h3>
    <div class="donghua2"></div>
    <pre>
      //
      //

        function donghua(obj, target) {
        obj.timer = setInterval(function () {
          if (obj.offsetLeft >= target) {
            clearInterval(obj.timer);
          }
          obj.style.left = obj.offsetLeft + 1 + "px";
        }, 30);
      }
      var box = document.querySelector(".donghua");
      clearInterval(obj.timer); //
      donghua(box, 600);
      <button @click="youhua"></button></pre>
    <h3></h3>
    <div class="biansu"></div>
    <pre>
      //  +  10
      //  + --> - /10
      function donghua(obj, target) {
        obj.timer = setInterval(function () {
          //
          var step = (target - obj.offsetLeft) / 10;
          if (obj.offsetLeft >= target) {
            clearInterval(obj.timer);
          }
          obj.style.left = obj.offsetLeft + step + "px";
        }, 30);
      }
      var box = document.querySelector(".donghua");
      clearInterval(box.timer); //
      donghua(box, 600);
        <button @click="biansu"></button></pre>
    <h3></h3>
    <li>Math.ceil() ()</li>
    <li>Math.floor() ()</li>
    <pre>
        function donghua(obj, target) {
        obj.timer = setInterval(function () {
          //
          //
          var step = (target - obj.offsetLeft) / 10;
          step > 0 ?Math.ceil(step):Math.floor(step)
          if (obj.offsetLeft >= target) {
            clearInterval(obj.timer);
          }
          obj.style.left = obj.offsetLeft + step + "px";
        }, 30);
      }
      var box = document.querySelector(".donghua");
      clearInterval(box.timer); //
      donghua(box, 600);
        </pre
    >
    <h3></h3>
    <pre>
      function donghua(obj, targetfun) {
        obj.timer = setInterval(function () {
          var step = (target - obj.offsetLeft) / 10;
          step > 0 ?Math.ceil(step):Math.floor(step)
          if (obj.offsetLeft >= target) {
            clearInterval(obj.timer);
            fun();//
          }
          obj.style.left = obj.offsetLeft + step + "px";
        }, 30);
      }
      var box = document.querySelector(".donghua");
      clearInterval(box.timer); 
      donghua(box, 600function(){});//
    </pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      flg: false,
    };
  },
  methods: {
    fengzhuang() {
      this.flg = !this.flg;
      var that = this;
      function donghua(obj, target) {
        var timer = setInterval(function () {
          if (obj.offsetLeft >= target) {
            clearInterval(timer);
            that.flg = !that.flg;
            obj.style.left = 400 + "px";
          }
          obj.style.left = obj.offsetLeft + 1 + "px";
        }, 30);
      }
      var box = document.querySelector(".donghua");
      donghua(box, 600);
    },
    youhua() {
      this.flg = !this.flg;
      var that = this;
      function donghua(obj, target) {
        obj.timer = setInterval(function () {
          if (obj.offsetLeft >= target) {
            clearInterval(obj.timer);
            that.flg = !that.flg;
            obj.style.left = 400 + "px";
          }
          obj.style.left = obj.offsetLeft + 1 + "px";
        }, 30);
      }
      var box = document.querySelector(".donghua");
      clearInterval(box.timer); //
      donghua(box, 600);
    },
    biansu() {
      this.flg = !this.flg;
      var that = this;
      function donghua(obj, target) {
        obj.timer = setInterval(function () {
          //
          var step = (target - obj.offsetLeft) / 10;
          if (obj.offsetLeft >= target) {
            clearInterval(obj.timer);
            that.flg = !that.flg;
            obj.style.left = 400 + "px";
          }
          obj.style.left = obj.offsetLeft + step + "px";
        }, 30);
      }
      var box = document.querySelector(".donghua");
      clearInterval(box.timer); //
      donghua(box, 600);
    },
    donghua(obj, target, fun) {
      obj.timer = setInterval(function () {
        var step = (target - obj.offsetLeft) / 10;
        step > 0 ? Math.ceil(step) : Math.floor(step);
        if (obj.offsetLeft >= target) {
          clearInterval(obj.timer);
          fun();
        }
        obj.style.left = obj.offsetLeft + step + "px";
      }, 30);
    },
  },
  computed: {},
  mounted() {},
};
</script>

<style>
.donghua {
  width: 100px;
  height: 100px;
  background-color: pink;
  position: absolute;
  top: 50px;
  left: 400px;
}
.ceshi {
  width: 200px;
  height: 40px;
  line-height: 40px;
  text-align: left;
  background-color: pink;
  position: absolute;
  top: 50px;
  left: 400px;
}
</style>