<template>
  <div class="Timeout">
    <h3></h3>
    <li>setTimeout()  </li>
    <pre>
      //setTimeout()()
      setTimeout(function () {
        console.log("1");
      }, 2000);

      //
      function timeout() {
        console.log("2");
      }
      setTimeout(timeout, 2000);

      //
      var timeout2 = function () {
        console.log("3");
      };
      setTimeout(timeout2, 2000);

      //
      var timer1 = setTimeout(timeout2, 2000);
     <button @click="timeout"></button></pre>
    <h3>--</h3>
    <div class="guangg"></div>
    <pre>
      var div = document.querySelector(".guangg");
      var btn = document.querySelector(".gb");
      btn.onclick = function () {
        setTimeout(function () {
          div.style.display = "none";
        }, 3000);
      };
       <button class="gb"></button></pre>
    <h3></h3>
    <li>setTimeout</li>
    <pre>
      function timeout() {
        console.log("");
      }
      var timer1 = setTimeout(timeout, 200000);
      var btn = document.querySelector(".stop");
      btn.onclick = function () {
        clearTimeout(timer1);
      };
      <button class="stop"></button></pre>
    <h3></h3>
    <li>setInterval(,) </li>
    <pre>
      var timer = setInterval(function () {
        console.log("");
      }, 2000);
      <button @click="dingshiqi()"></button></pre>
    <h3></h3>
    <li>setInterval</li>
    <pre>
      function timeout() {
        console.log("");
      }
      var timer1 = setInterval(timeout, 2000);
      var btn = document.querySelector(".stop2");
      btn.onclick = function () {
        clearInterval(timer1);
      };
      <button class="stop2"></button></pre>
    <h3>--</h3>
    <p class="daojishi"></p>
    <pre>
      //  
      var p = document.querySelector(".daojishi");
      var time = 5;
      var timer = setInterval(function () {
        if (time == -1) {
          clearInterval(timer);
          p.style.display = "none";
        } else {
          p.innerText = time;
          time--;
        }
      }, 1000);
      <button @click="daojishi"></button></pre>
    <h3>-60</h3>
    <div class="duanxin"><input type="text" /><button></button></div>
    <pre>
      var div = document.querySelector(".duanxin");
      var btn = div.children[1];
      var time = 5;
      btn.onclick = function () {
        btn.disabled = true;
        timeout();
        var timer1 = setInterval(timeout, 1000);
        function timeout() {
          if (time != 0) {
            btn.innerText = time;
            time--;
          } else {
            btn.innerText = "";
            btn.disabled = false;
            clearInterval(timer1);
            time = 5;
          }
        }
      };
    </pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    timeout() {
      //setTimeout()()
      setTimeout(function () {
        console.log("1");
      }, 2000);

      //
      function timeout() {
        console.log("2");
      }
      setTimeout(timeout, 2000);

      //
      var timeout2 = function () {
        console.log("3");
      };
      setTimeout(timeout2, 2000);

      //
      var timer1 = setTimeout(timeout2, 2000);
    },
    guangg() {
      var div = document.querySelector(".guangg");
      var btn = document.querySelector(".gb");
      btn.onclick = function () {
        setTimeout(function () {
          div.style.display = "none";
        }, 3000);
      };
    },
    tingzhi() {
      function timeout() {
        console.log("");
      }
      var timer1 = setTimeout(timeout, 200000);
      var btn = document.querySelector(".stop");
      btn.onclick = function () {
        clearTimeout(timer1);
      };
    },
    dingshiqi() {
      var timer = setInterval(function () {
        console.log("");
      }, 2000);
    },
    daojishi() {
      var p = document.querySelector(".daojishi");
      var time = 5;
      var timer = setInterval(function () {
        if (time == -1) {
          clearInterval(timer);
          p.style.display = "none";
        } else {
          p.innerText = time;
          time--;
        }
      }, 1000);
    },
    dsq() {
      function timeout() {
        console.log("");
      }
      var timer1 = setInterval(timeout, 2000);
      var btn = document.querySelector(".stop2");
      btn.onclick = function () {
        clearInterval(timer1);
      };
    },
    duanxin() {
      var div = document.querySelector(".duanxin");
      var btn = div.children[1];
      var time = 5;
      btn.onclick = function () {
        btn.disabled = true;
        timeout();
        var timer1 = setInterval(timeout, 1000);
        function timeout() {
          if (time != 0) {
            btn.innerText = time;
            time--;
          } else {
            btn.innerText = "";
            btn.disabled = false;
            clearInterval(timer1);
            time = 5;
          }
        }
      };
    },
  },
  mounted() {
    this.guangg();
    this.tingzhi();
    this.dsq();
    this.duanxin();
  },
  computed: {},
};
</script>

<style lang="less" scoped>
.guangg {
  width: 100px;
  height: 100px;
  background-color: pink;
  text-align: center;
  line-height: 100px;
}
.duanxin {
  width: 200px;
  overflow: hidden;
  input {
    width: 140px;
    float: left;
  }
  button {
    width: 50px;
    float: right;
  }
}
</style>