<template>
  <div class="Buchong">
    <h3></h3>
    <pre><img src="../assets/on.png" ></pre>
    <h3></h3>
    <pre>
    //
    document.addEventListener("contextmenu", function (e) {
      e.preventDefault();
    });
    //
    document.addEventListener("selectstart", function (e) {
      e.preventDefault();
    });
    </pre>
    <h3></h3>
    <pre>
      //e
      e.clientX X
      e.clientY Y
      e.pageX X
      e.pageY Y
      e.screenX X
      e.screenY Y
    </pre>
    <h3></h3>
    <img src="../assets/jlt.png" class="img" />
    <pre>
      var img = document.querySelector(".img");
      document.onmousemove = function (e) {
        console.log(e.clientX, e.clientY);
        img.style.top = e.clientY - 25 + "px";
        img.style.left = e.clientX - 25 + "px";
      };
      <button @click="tianshi"></button></pre>
    <h3></h3>
    <pre>
        onkeyup       //
        onkeydown   //
        onkeypress  // 
        
        //event
        keycodeASCII  
        </pre
    >
    <h3>-s</h3>
    <input type="text" id="shuru" />
    <pre>
      var input = document.querySelector("#shuru");
      document.onkeyup = function (e) {
        if (e.keyCode == 83) {
          input.focus();
        }
      };</pre
    >
    <h3>-</h3>
    <br />
    <div class="fangda">
      <input type="text" />
      <p></p>
    </div>
    <pre>
      var input = document.querySelector(".fangda").querySelector("input");
      var p = input.nextElementSibling;
      input.onkeyup = function () {
        if (input.value != "") {
          p.style.display = "block";
          p.innerText = input.value;
        } else {
          p.style.display = "none";
        }
      };
      input.onblur = function (e) {
        p.style.display = "none";
      };</pre
    >
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    tianshi() {
      var img = document.querySelector(".img");
      document.onmousemove = function (e) {
        console.log(e.clientX, e.clientY);
        img.style.top = e.clientY - 25 + "px";
        img.style.left = e.clientX - 25 + "px";
      };
    },
    jujiao() {
      var input = document.querySelector("#shuru");
      document.onkeyup = function (e) {
        if (e.keyCode == 83) {
          input.focus();
        }
      };
    },
    fangda() {
      var input = document.querySelector(".fangda").querySelector("input");
      var p = input.nextElementSibling;
      input.onkeyup = function () {
        if (input.value != "") {
          p.style.display = "block";
          p.innerText = input.value;
        } else {
          p.style.display = "none";
        }
      };
      input.onblur = function (e) {
        p.style.display = "none";
      };
    },
  },
  mounted() {
    this.jujiao();
    this.fangda();
  },
};
</script>

<style lang="less" >
.img {
  position: fixed;
  top: 100;
  left: 0;
  background-color: pink;
  width: 50px;
  height: 50px;
}
</style>