<template>
  <div class="Shijianliu">
    <h3></h3>
    <p></p>
    <p>DOMhtmlheaddiv</p>
    <p>div</p>
    <p>div</p>
    <p></p>
    <ul>
      <li></li>
      <li></li>
      <li></li>
    </ul>
    <h3></h3>
    <li></li>
    <div class="wai">
      <div class="nei"></div>
    </div>
    <pre>
      //
      var wai = document.querySelector(".wai");
      var nei = document.querySelector(".nei");
      //true
      wai.addEventListener(
        "click",
        function () {
          alert("");
        },
        true
      );
      nei.addEventListener(
        "click",
        function () {
          alert("");
        },
        true
      );</pre
    >
    <h3></h3>
    <li></li>
    <li>onclick</li>
    <div class="wai2">
      <div class="nei2"></div>
    </div>
    <pre>
      //
      var wai = document.querySelector(".wai2");
      var nei = document.querySelector(".nei2");
      //false
      wai.addEventListener(
        "click",
        function () {
          alert("");
        },
        false //false
      );
      nei.addEventListener(
        "click",
        function () {
          alert("");
        },
        false
      );
    </pre>
    <h3></h3>
    <pre>
      var a = document.querySelector(".btn");
      //
      //event
      a.onclick = function (event) {
        console.log(event);
      };
      //
      a.addEventListener("click", function (event) {
        console.log(event);
      });
      <button class="btn"></button>
    </pre>
    <h3></h3>
    <ul>
      <li>e.target </li>
      <li>e.type  click mouseover</li>
      <li>e.preventDefault()  </li>
      <li>e.stopPropagation() </li>
    </ul>
    <pre>
      var btn = document.querySelector("#ck");
      btn.onclick = function (e) {
        console.log(e.target); //
        console.log(e.type); //
        e.preventDefault(); //
        e.stopPropagation(); //
      };
      <button id="ck"></button></pre>
    <h3></h3>
    <ul class="weipai">
      <li></li>
      <li></li>
      <li></li>
    </ul>
    <pre>
      //
      var ul = document.querySelector(".weipai");
      ul.onclick = function (e) {
        console.log(e.target.innerText);
      };</pre
    >
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    shijianliu() {
      //
      var wai = document.querySelector(".wai");
      var nei = document.querySelector(".nei");
      //true
      wai.addEventListener(
        "click",
        function () {
          alert("");
        },
        true
      );
      nei.addEventListener(
        "click",
        function () {
          alert("");
        },
        true
      );
    },
    shijianliu2() {
      //
      var wai = document.querySelector(".wai2");
      var nei = document.querySelector(".nei2");
      //false
      wai.addEventListener(
        "click",
        function () {
          alert("");
        },
        false //false
      );
      nei.addEventListener(
        "click",
        function () {
          alert("");
        },
        false
      );
    },
    shijianduixiang() {
      var a = document.querySelector(".btn");
      //
      //event
      a.onclick = function (event) {
        console.log(event);
      };
      //
      a.addEventListener("click", function (event) {
        console.log(event);
      });
    },
    shijian() {
      var btn = document.querySelector("#ck");
      btn.onclick = function (e) {
        console.log(e.target); //
        console.log(e.type); //
        e.preventDefault(); //
        e.stopPropagation(); //
      };
    },
    weipai() {
      var ul = document.querySelector(".weipai");
      ul.onclick = function (e) {
        console.log(e.target.innerText);
      };
    },
    
  },
  mounted() {
    this.shijianliu();
    this.shijianliu2();
    this.shijianduixiang();
    this.shijian();
    this.weipai();
    
  },
  computed: {},
};
</script>

