<template>
  <div class="Carousel">
    <h3></h3>
    <div class="lunbo">
      <div class="content" @mouseenter="jinru" @mouseleave="chuqu">
        <div class="div1"></div>
        <div class="div2"></div>
        <div class="div3"></div>
      </div>
      <div class="left" @click="zuo">&lt;</div>
      <div class="right" @click="you">&gt;</div>
      <div>
        <ul class="list"></ul>
      </div>
    </div>
    <br /><br /><br />
    <h3></h3>
    <li>html</li>
    <pre v-text="html"></pre>
    <li>css</li>
    <pre v-text="css"></pre>
    <li></li>
    <pre>
      donghua(obj, target, fun) {
      clearInterval(obj.timer);
      obj.timer = setInterval(function () {
        var step = (target - obj.offsetLeft) / 10;
        step = step > 0 ? Math.ceil(step) : Math.floor(step);
        if (obj.offsetLeft == target) {
          clearInterval(obj.timer);
          if (fun) {
            fun();
          }
        }
        obj.style.left = obj.offsetLeft + step + "px";
      }, 15);
    },
    </pre>
    <h3></h3>
    <pre>
      1.
      2.
      3.
      4.

      //3
    </pre>
    <h3>3.1</h3>
    <pre>
      //
      var content = document.querySelector(".content");
      var lunbo = content.querySelectorAll("div").length;
      //
      var list = document.querySelector(".list");
      //lilist
      for (var i = 0; i < lunbo; i++) {
        //li
        var addli = document.createElement("li");
        //liclass
        addli.className = "li";
        //li
        list.appendChild(addli);
      }
    </pre>
    <h3>3.2</h3>
    <pre>
      //li
      document.querySelector(".list").children[0].className = "dangqian";
      //li
      for (var i = 0; i < list.children.length; i++) {
        //li
        list.children[i].onclick = function () {
          //
          for (var i = 0; i < list.children.length; i++) {
            list.children[i].className = "li";
          }
          //
          this.className = "dangqian";
        };
      }
    </pre>
    <h3>3.3</h3>
    <pre>
      //  index
      addli.className = "li";
      addli.setAttribute("index", i); //&lt;-------1.3index
      //li
      list.appendChild(addli);


      //liindex
      list.children[i].onclick = function () {
          //
          for (var i = 0; i < list.children.length; i++) {
            list.children[i].className = "li";
          }
          //
          this.className = "dangqian";

          //1.3
          //index
          var index = this.getAttribute("index");
          //
          donghua(content, index * -content.children[index].offsetWidth);
        };
    </pre>
    <h3>1.1</h3>
    <pre>
      var ul = document.querySelector(".content");
      var liWidth = ul.children[0].offsetWidth;
      // num

      //
      num++; //num++
      donghua(ul, -num * liWidth);//
    </pre>
    <h3>1.2</h3>
    <pre>
      // num++; 
      //
      if (num == ul.children.length - 1) {
        ul.style.left = 0;
        num = 0;
      }
      //bug  
    </pre>
    <h3>1.3()</h3>
    <pre>
       //1.6 
      var cont = document.querySelector(".content"); //()
      var first = cont.children[0].cloneNode(true); //true
      document.querySelector(".content").appendChild(first); //
      //,
    </pre>
    <h3>2.1</h3>
    <pre>
       var ol = document.querySelector(".list");
      // dian = 0 

      //dian 
      dian++ //
      //
      if (dian == ol.children.length) {
        dian = 0;
      }
      // 
       for (var i = 0; i < ol.children.length; i++) {
        ol.children[i].className = "li";
      }
      //
      ol.children[dian].className = "dangqian";
    </pre>
    <h3>2.2bug</h3>
    <ol>
      <li>num</li>
      <li> dian</li>
    </ol>
    <pre>
      //1. indexnum 
      //

      //index
      var index = this.getAttribute("index");
      num = index; //2.2bug

      //2. indexdian
      //index
      var index = this.getAttribute("index");
      num = index; //2.2bug
      dian = index;//2.2bug
    </pre>
    <h3>1.4</h3>
    <pre>
      var ul = document.querySelector(".content");
      var liWidth = ul.children[0].offsetWidth;
      var ol = document.querySelector(".list");
      if (num == 0) {
        num = ul.children.length - 1;//
        ul.style.left = -num * liWidth + "px";//
      }
      num--;//num--
      donghua(ul, -num * liWidth);
      dian--;//dian--
      if (dian < 0) {//0
        dian = ol.children.length - 1;//
      }
      for (var i = 0; i < ol.children.length; i++) {
        ol.children[i].className = "li";
      }
      ol.children[dian].className = "dangqian";
    </pre>
    <h3>4.1</h3>
    <pre>
      var timer = setInterval(function () {
        //document
        //.click() 
      }, 2000);

      //contentmouseentermouseleave
      //
       clearInterval(timer);
      timer = null;

      //
      var timer = setInterval(function () {
        //document
        //.click() 
      }, 2000);
    </pre>
    <h3>bug-</h3>
    <pre>
      // true false
      //flag flagtrue

      //
       if (flag) {
        flag = false; //
        //
        //
        donghua(ul, -num * liWidth, function () {
          flag = true; //
        });
        }
    </pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      num: 0,
      dian: 0,
      flag: true,
      //
      html: `
    <!--  -->
    <div class="lunbo">
      <!--  -->
      <div class="content">
        <div class="div1"></div>
        <div class="div2"></div>
        <div class="div3"></div>
      </div>
      <!--  -->
      <div class="left">&lt;</div>
      <div class="right">&gt;</div>
      <!--  -->
      <div>
        <!-- ulli -->
        <ul class="list"></ul>
      </div>
    </div>`,
      css: `
  //
  .lunbo {
  position: relative;
  width: 800px;
  height: 500px;
  overflow: hidden;
  background-color: rgba(255, 192, 203, 0.4);
  margin: 0 auto;
  .content {
    position: absolute;
    top: 0;
    left: 0;
    width: 3500px;
    height: 100%;
    div[class^="div"] {
      width: 800px;
      height: 500px;

      float: left;
      &:nth-child(1) {
        background-color: red;
      }
      &:nth-child(2) {
        background-color: pink;
      }
      &:nth-child(3) {
        background-color: blue;
      }
      &:nth-child(4) {
        background-color: red;
      }
    }
  }
  .left,
  .right {
    cursor: pointer;
    display: flex;
    display: none;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 80px;
    background-color: rgba(0, 0, 0, 0.2);
  }
  .left {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
  .right {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
  }
  .list {
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 20px;
    .li {
      list-style: none;
      width: 6px;
      height: 6px;
      border-radius: 3px;
      background-color: #ccc;
      margin-right: 10px;
    }
  }
  &:hover .left {
    display: flex;
  }
  &:hover .right {
    display: flex;
  }
}

.li {
  list-style: none;
  width: 6px;
  height: 6px;
  border-radius: 3px;
  background-color: #ccc;
  margin-right: 10px;
}
.dangqian {
  list-style: none;
  width: 6px;
  height: 6px;
  border-radius: 3px;
  margin-right: 10px;
  background-color: #000;
}`,
    };
  },

  methods: {
    jinru() {
      clearInterval(this.timer);
      this.timer = null;
    },
    chuqu() {
      var that = this;
      this.timer = setInterval(function () {
        that.you();
      }, 2000);
    },
    donghua(obj, target, fun) {
      clearInterval(obj.timer);
      obj.timer = setInterval(function () {
        var step = (target - obj.offsetLeft) / 10;
        step = step > 0 ? Math.ceil(step) : Math.floor(step);
        if (obj.offsetLeft == target) {
          clearInterval(obj.timer);
          if (fun) {
            fun();
          }
        }
        obj.style.left = obj.offsetLeft + step + "px";
      }, 15);
    },
    you() {
      if (this.flag) {
        this.flag = false; //
        var ul = document.querySelector(".content");
        var liWidth = ul.children[0].offsetWidth;
        var ol = document.querySelector(".list");
        if (this.num == ul.children.length - 1) {
          ul.style.left = 0;
          this.num = 0;
        }
        this.num++;
        var that = this;
        this.donghua(ul, -this.num * liWidth, function () {
          that.flag = true; //
        });
        this.dian++;
        if (this.dian == ol.children.length) {
          this.dian = 0;
        }
        for (var i = 0; i < ol.children.length; i++) {
          ol.children[i].className = "li";
        }
        //
        ol.children[this.dian].className = "dangqian";
        //this.className = "dangqian";
      }
    },
    zuo() {
      if (this.flag) {
        this.flag = false; //
        var ul = document.querySelector(".content");
        var liWidth = ul.children[0].offsetWidth;
        var ol = document.querySelector(".list");
        if (this.num == 0) {
          this.num = ul.children.length - 1; //
          ul.style.left = -this.num * liWidth + "px"; //
        }
        this.num--; //num--
        var that = this;
        this.donghua(ul, -this.num * liWidth, function () {
          that.flag = true;
        });
        this.dian--; //dian--
        if (this.dian < 0) {
          //0
          this.dian = ol.children.length - 1; //
        }
        for (var i = 0; i < ol.children.length; i++) {
          ol.children[i].className = "li";
        }
        //
        ol.children[this.dian].className = "dangqian";
        //this.className = "dangqian";
      }
    },

    //
    main() {
      //1.1
      //
      var content = document.querySelector(".content");
      var lunbo = content.querySelectorAll("div").length;
      //
      var list = document.querySelector(".list");
      //lilist
      for (var i = 0; i < lunbo; i++) {
        //li
        var addli = document.createElement("li");
        //liclass
        addli.className = "li";
        addli.setAttribute("index", i); //1.3index
        //li
        list.appendChild(addli);
      }

      //1.2
      //li
      document.querySelector(".list").children[0].className = "dangqian";
      //li
      for (var i = 0; i < list.children.length; i++) {
        //li
        var that = this;
        list.children[i].onclick = function () {
          //
          for (var i = 0; i < list.children.length; i++) {
            list.children[i].className = "li";
          }
          //
          this.className = "dangqian";
          //1.3
          //index
          var index = this.getAttribute("index");
          this.num = index; //2.2bug
          this.dian = index; //2.2bug
          //
          that.donghua(content, index * -content.children[index].offsetWidth);
        };
      }

      //1.3 

      //1.4 

      //1.5 

      //1.6 
      var cont = document.querySelector(".content"); //()
      var first = cont.children[0].cloneNode(true); //true
      document.querySelector(".content").appendChild(first); //
      //,

      //4 
      //
      var that = this;
      this.timer = setInterval(function () {
        that.you();
      }, 2000);
    },
  },
  computed: {},
  mounted() {
    this.main();
  },
  /*  mounted() {
    //
    var content = document.querySelector(".content");
    var lunbo = document
      .querySelector(".content")
      .querySelectorAll("div").length;
    //
    var list = document.querySelector(".list");
    //lilist
    for (var i = 0; i < lunbo; i++) {
      var addli = document.createElement("li");
      addli.className = "li";
      addli.setAttribute("index", i);

      list.appendChild(addli);
    }
    //li
    document.querySelector(".list").children[0].className = "dangqian";
    //li
    for (var i = 0; i < list.children.length; i++) {
      //li
      var that = this;
      list.children[i].onclick = function () {
        //
        for (var i = 0; i < list.children.length; i++) {
          list.children[i].className = "li";
        }
        //
        this.className = "dangqian";
        var index = this.getAttribute("index");
        that.num = index;
        that.dian = index;
        console.log(-content.children[index].offsetWidth);
        that.donghua(content, index * -content.children[index].offsetWidth);
      };
    }
    //
    var first = document.querySelector(".content").children[0].cloneNode(true);
    document.querySelector(".content").appendChild(first);

    //
    var that = this;
    this.timer = setInterval(function () {
      that.you();
    }, 2000);
  }, */
  destroyed() {
    clearInterval(this.timer);
  },
};
</script>

