<template>
  <div class="Nodedemo">
    <h2>/</h2>
    <p>nodeType() nodeName() nodeValue()</p>
    <p>nodeType 1 2 3()</p>
    <br />
    <h3></h3>
    <li>parentNode</li>
    <div> <span class="zjd"></span></div>
    <pre>
      var a = document.querySelector(".zjd");
      var b = a.parentNode;
      console.log(b);//
      <button @click="fjd"></button></pre>
    <h3></h3>
    <li>childNodes</li>
    <div class="fjd"> <span></span></div>
    <pre>
      var a = document.querySelector(".fjd");
      var b = a.childNodes;
      console.log(b); ////
      <button @click="zjd"></button></pre>
    <h3>()</h3>
    <li>children</li>
    <div class="fjd"> <span></span></div>
    <pre>
      var a = document.querySelector(".fjd");
      var b = a.children;
      console.log(b); //
      <button @click="zjdnice"></button> </pre>
    <h3>/</h3>
    <li>firstChild</li>
    <li>lastChild</li>
    <div class="first">
      1
      <div></div>
      <span></span>
      <a href=""></a>
    </div>
    <pre>    var a = document.querySelector(".first");
      console.log(a.firstChild);
      console.log(a.lastChild);
      <button @click="first"></button></pre>
    <h3>/()</h3>
    <li>firstElementChild</li>
    <li>lastElementChild</li>
    <pre>    var a = document.querySelector(".first");
      console.log(a.firstElementChild);
      console.log(a.lastElementChild);
      <button @click="firstnice"></button></pre>
    <h3></h3>
    <pre>
      var a = document.querySelector(".first");
      console.log(a.children[0]);
      console.log(a.children[a.children.length-1]);
      <button @click="jrx"></button></pre>
    <h3></h3>
    <ul class="xiala">
      <li>
        <div></div>
        <ul>
          <li></li>
          <li></li>
        </ul>
      </li>
      <li>
        <div></div>
        <ul>
          <li></li>
          <li></li>
        </ul>
      </li>
    </ul>
    <pre>
      var a = document.querySelector(".xiala");
      var b = a.children;
      for (var i = 0; i < b.length; i++) {
        b[i].onmouseover = function () {
          this.children[1].style.display = "block";
        };
        b[i].onmouseout = function () {
          this.children[1].style.display = "none";
        };
      }
      <button @click="xiala"></button></pre>
    <h3></h3>
    <li>nextSibling</li>
    <div class="xiongdi"></div>
    1
    <span></span>
    <pre>
      var a = document.querySelector(".xiongdi");
      console.log(a.nextSibling);//
      <button @click="xiongdi"></button></pre>
    <h3>()</h3>
    <li>nextElementSibling</li>
    <pre>
       var a = document.querySelector(".xiongdi");
       console.log(a.nextElementSibling); 
      <button @click="xiongdinice"></button></pre>
    <h3></h3>
    <li>previousSibling</li>
    <div></div>
    1
    <span class="sygxiongdi"></span>
    <pre>
      var a = document.querySelector(".sygxiongdi");
      console.log(a.previousSibling);//
      <button @click="sygxiongdi"></button>
    </pre>
    <h3>()</h3>
    <li>previousElementSibling</li>
    <pre>
      var a = document.querySelector(".sygxiongdi");
      console.log(a.previousElementSibling); 
      <button @click="sygxiongdinice"></button></pre>
    <h3></h3>
    <p></p>
    <pre>
  function getNextElementsibling(element) {
      var el = element;
      while ((el = el.nextSibling)) {
        if (el.nodeType === 1) {
          return el;
        }
      }
      return null;
    }</pre
    >
    <h3>()</h3>
    <li>appendChild()</li>
    <ul class="addnode">
      1
    </ul>
    <pre>    //
      var a = document.createElement("li");
      //
      var b = document.querySelector(".addnode");
      b.appendChild(a);
      <button @click="addnode"></button></pre>
    <h3>()</h3>
    <li>insertBefore()</li>
    <pre>    //
      var a = document.createElement("li");
      //
      var b = document.querySelector(".addnode");
      b.insertBefore(a, b.children[0]);
      <button @click="befonode"></button></pre>
    <h3></h3>
    <div>
      <input type="text" name="" id="wenben" /><button id="fabu"></button>
      <ul id="ulneir">
        <li>123</li>
      </ul>
    </div>
    <pre>
      var ipt = document.querySelector("#wenben");
      var btn = document.querySelector("#fabu");
      var ul = document.querySelector("#ulneir");
      btn.onclick = function () {
        var a = document.createElement("li");
        a.innerHTML = ipt.value;
        ul.appendChild(a);
      };</pre
    >
    <h3></h3>
    <li>removeChild</li>
    <ul class="ulxiongda">
      <li></li>
      <li></li>
    </ul>
    <pre>
      var a = document.querySelector(".ulxiongda");
      console.log(a);
      if (a.children.length == 0) return alert("");
      a.removeChild(a.children[0]);
      <button @click="delli"></button></pre>
    <h3></h3>
    <div>
      <input type="text" name="" id="wenben2" /><button id="fabu2"></button>
      <ul id="ulneir2"></ul>
    </div>
    <pre>
      var ipt = document.querySelector("#wenben2");
      var btn = document.querySelector("#fabu2");
      var ul = document.querySelector("#ulneir2");
      btn.onclick = function () {
        var a = document.createElement("li");
        if (ipt.value == "") {
          alert("");
          return;
        }
        a.innerHTML = ipt.value + "<a href='javascript:;'></a>";
        ul.appendChild(a);
        //
        var b = ul.querySelectorAll("a");
        for (var i = 0; i < b.length; i++) {
          b[i].onclick = function () {
            ul.removeChild(this.parentNode);
          };
        }
      };
      </pre>
    <h3></h3>
    <li>cloneNode(true/false)</li>
    <p>false,</p>
    <ul class="clone">
      <li></li>
    </ul>
    <pre>    var a = document.querySelector(".clone");
      var b = a.children[0].cloneNode(true);
      a.appendChild(b);
      <button @click="clone"></button></pre>
    <h3></h3>
    <ul>
      <li>document.write </li>
      <li>innerHTML DOM</li>
      <li>
        innerHTML 
        
      </li>
      <li>createElement  </li>
    </ul>
      <pre>
    //a
    //
    var inner = document.querySelector(".inner");
    inner.innerHTML += "<a href='#'></a>";
    //
    var create = document.querySelector(".create");
    var a = document.createElement("a");
    create.appendChild("a");
    //
    var arr = [];
    arr.push("<a href='#'></a>");
    inner.innerHTML = arr.join("");
      </pre>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    fjd() {
      var a = document.querySelector(".zjd");
      var b = a.parentNode;
      console.log(b); //
    },
    zjd() {
      var a = document.querySelector(".fjd");
      var b = a.childNodes;
      console.log(b); ////
    },
    zjdnice() {
      var a = document.querySelector(".fjd");
      var b = a.children;
      console.log(b); //
    },
    first() {
      var a = document.querySelector(".first");
      console.log(a.firstChild);
      console.log(a.lastChild);
    },
    firstnice() {
      var a = document.querySelector(".first");
      console.log(a.firstElementChild);
      console.log(a.lastElementChild);
    },
    jrx() {
      var a = document.querySelector(".first");
      console.log(a.children[0]);
      console.log(a.children[a.children.length - 1]);
    },
    xiala() {
      var a = document.querySelector(".xiala");
      var b = a.children;
      for (var i = 0; i < b.length; i++) {
        b[i].onmouseover = function () {
          this.children[1].style.display = "block";
        };
        b[i].onmouseout = function () {
          this.children[1].style.display = "none";
        };
      }
    },
    xiongdi() {
      var a = document.querySelector(".xiongdi");
      console.log(a.nextSibling); //
    },
    xiongdinice() {
      var a = document.querySelector(".xiongdi");
      console.log(a.nextElementSibling);
    },
    sygxiongdi() {
      var a = document.querySelector(".sygxiongdi");
      console.log(a.previousSibling); //
    },
    sygxiongdinice() {
      var a = document.querySelector(".sygxiongdi");
      console.log(a.previousElementSibling);
    },
    addnode() {
      //
      var a = document.createElement("li");
      //
      var b = document.querySelector(".addnode");
      b.appendChild(a);
    },
    befonode() {
      //
      var a = document.createElement("li");
      //
      var b = document.querySelector(".addnode");
      b.insertBefore(a, b.children[0]);
    },
    liuyan() {
      var ipt = document.querySelector("#wenben");
      var btn = document.querySelector("#fabu");
      var ul = document.querySelector("#ulneir");
      btn.onclick = function () {
        var a = document.createElement("li");
        if (ipt.value == "") {
          alert("");
          return;
        }
        a.innerHTML = ipt.value;
        ul.appendChild(a);
      };
    },
    delli() {
      var a = document.querySelector(".ulxiongda");
      console.log(a);
      if (a.children.length == 0) return alert("");
      a.removeChild(a.children[0]);
    },
    dellyb() {
      var ipt = document.querySelector("#wenben2");
      var btn = document.querySelector("#fabu2");
      var ul = document.querySelector("#ulneir2");
      btn.onclick = function () {
        var a = document.createElement("li");
        if (ipt.value == "") {
          alert("");
          return;
        }
        a.innerHTML = ipt.value + "<a href='javascript:;'></a>";
        ul.appendChild(a);
        //
        var b = ul.querySelectorAll("a");
        for (var i = 0; i < b.length; i++) {
          b[i].onclick = function () {
            ul.removeChild(this.parentNode);
          };
        }
      };
    },
    clone() {
      var a = document.querySelector(".clone");
      var b = a.children[0].cloneNode(true);
      a.appendChild(b);
    },
  },
  mounted() {
    this.liuyan();
    this.dellyb();
  },
  computed: {},
};
</script>

<style lang="less" scoped>
.xiala {
  overflow: hidden;
  list-style: none;
  > li {
    list-style: none;
    float: left;
    margin-left: 50px;
    ul,
    li {
      padding: 0;
      list-style: none;
    }
    ul {
      display: none;
    }
  }
}
</style>