<template>
  <div class="Domdemo">
    <h3>()</h3>
    <pre>
      var a = document.querySelector(".ck");
      //
      a.onclick = function () {
        console.log("");
      }; // 

      //
      a.addEventListener("click", function () {
        console.log("");
      });
      <button class="ck" @click="bdsj"></button></pre>
    <h3>()</h3>
    <pre>
      var a = document.querySelector(".jiebang");
      //
      a.onclick = function () {
        console.log("");
        a.onclick = null;
      };
      //
      function fu() {
        console.log("");
        a.removeEventListener("click", fu);
      }
      a.addEventListener("click", fu);
        <button class="jiebang"></button>
      </pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    bdsj() {
      var a = document.querySelector(".ck");
      //
      a.onclick = function () {
        console.log("");
      }; // 

      //
      a.addEventListener("click", function () {
        console.log("");
      });
    },
    jiebang() {
      var a = document.querySelector(".jiebang");
      //
      a.onclick = function () {
        console.log("");
        a.onclick = null;
      };
      //
      function fu() {
        console.log("");
        a.removeEventListener("click", fu);
      }
      a.addEventListener("click", fu);
    },
  },
  mounted() {
    this.jiebang();
  },
  computed: {},
};
</script>

