<template>
  <div class="Intdemo">
    <h3></h3>
    <pre>
      <input type="text" index="1" id="zdy" />
      var a = document.querySelector("#zdy");
      var b = a.getAttribute("index");
      console.log(b);
      <button @click="zdy"></button></pre>
    <h3>/</h3>
    <pre>
        <input type="text" id="setzdy" index="1">
         var a = document.querySelector("#setzdy");
         a.setAttribute("index", "2");
         console.log(a.getAttribute("index"));
        <button @click="setzdy"></button></pre>
    <h3></h3>
    <pre>
      <input type="text" index="1" id="del">
      var a = document.querySelector("#del");
      a.removeAttribute("index");
      <button @click="del"></button></pre>
    <h3></h3>
    <li>data-</li>
    <h3>data</h3>
    <pre>
        <input type="text" id="xinadd" data-index = "1">
      var a = document.querySelector("#xinadd");
      //datasetdata
      console.log(a.dataset.index);
      //data-list-anni
      a.dataset.listAnni
        <button @click="xinadd"></button>
      </pre>
    <h3>tab</h3>
    <div class="tab">
      <ul>
        <li class="tabsty"></li>
        <li></li>
        <li></li>
      </ul>
      <br />
      <div class="block"></div>
      <div></div>
      <div></div>
    </div>
    <pre>
      //
      var a = document.querySelector(".tab");
      var b = a.querySelectorAll("li");
      var c = a.querySelectorAll("div");
      for (var i = 0; i < b.length; i++) {
        //tab
        b[i].setAttribute("index", i);
        //
        b[i].onclick = function () {
          //
          for (var j = 0; j < b.length; j++) {
            b[j].className = "";
          }
          //
          this.className = "tabsty";
          //index
          var index = this.getAttribute("index");
          //
          for (var i = 0; i < b.length; i++) {
            c[i].style.display = "none";
          }
          c[index].style.display = "block";
        };
      }</pre
    >
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    zdy() {
      var a = document.querySelector("#zdy");
      var b = a.getAttribute("index");
      console.log(b);
    },
    setzdy() {
      var a = document.querySelector("#setzdy");
      a.setAttribute("index", "2");
      console.log(a.getAttribute("index"));
    },
    del() {
      var a = document.querySelector("#del");
      a.removeAttribute("index");
    },
    xinadd() {
      var a = document.querySelector("#xinadd");
      console.log(a.dataset.index);
    },
    tab() {
      //
      var a = document.querySelector(".tab");
      var b = a.querySelectorAll("li");
      var c = a.querySelectorAll("div");
      for (var i = 0; i < b.length; i++) {
        //tab
        b[i].setAttribute("index", i);
        //
        b[i].onclick = function () {
          //
          for (var j = 0; j < b.length; j++) {
            b[j].className = "";
          }
          //
          this.className = "tabsty";
          //index
          var index = this.getAttribute("index");
          //
          for (var i = 0; i < b.length; i++) {
            c[i].style.display = "none";
          }
          c[index].style.display = "block";
        };
      }
    },
  },
  mounted() {
    this.tab();
  },
  computed: {},
};
</script>

<style lang="less" scoped>
.tab {
  padding-left: 30px;
  overflow: hidden;
  li {
    list-style: none;
    float: left;
    margin-right: 20px;
    cursor: pointer;
  }
  .block {
    display: block;
  }
  div {
    display: none;
  }
}
.tabsty {
  background-color: red;
  color: #fff;
}
</style>